import React, { useState,useContext } from 'react';
import axios from "axios"
import {FetchContext, FetchProvider} from "./context/FetchContext"
import  { AuthContext } from "./context/AuthContext";


function EditProfile() {


    const fauth = useContext(FetchContext);
    const auth = useContext(AuthContext);

    const name = auth.authState.userInfo.name.split(" ")

    const [FormData, SetFormData] = useState(
        {
            firstName:name[0],
            lastName:name[1],
            email:auth.authState.userInfo.email
        }
    )
    function handleChange(event) {
        const { name, value } = event.target;
        console.log("Agent form", name, value);
        SetFormData((prev) => {
            return {
                ...prev,
                [name]: value
            }
        })

    }

    async function HandleSubmit(e) {
        e.preventDefault();
        console.log("All Event Data ", FormData)

        try
        {
            const del = await fauth.authAxios.patch("/clients",FormData);
            console.log(del);
            alert("Updated");
        }
        catch(er)
        {
            console.log(er)
        }
    }

   

    return (
        <div className="container container-fluid FluidStyle">
            <form style={{color:"black"}} onSubmit={HandleSubmit}>

            <input style={{color:"black"}} onChange={handleChange} name="firstName" type="text" value = {FormData.firstName} placeholder={name[0]} />
                <input style={{color:"black"}} onChange={handleChange} name="lastName" value = {FormData.lastName} type="text" placeholder={name[1]}/>
                <input style={{color:"black"}} onChange={handleChange} name="email" type="text" value = {FormData.email} placeholder={auth.authState.userInfo.email} />
                <button style={{color:"white",background:"#413c69"}} type="submit">Submit</button>
            </form>

        </div>
    )
}
export default EditProfile;