import React,{useContext} from "react";
import {Link} from "react-router-dom"
import  { AuthContext } from "./context/AuthContext";


const NavbarStyle={
  position:"fixed",
  right:"0",
  left:"0",
  borderRadius:"0px",
  minHeight:"70px",
  zIndex:"99",
  background:"#F5F5F5",
  color:"black",
  border:"0px",
  
}


function NavBar()
{
   const auth = useContext(AuthContext);

   return ( 
   <nav className="navbar navbar-inverse" style={NavbarStyle}>
        <div className="container-fluid">
          <div className="navbar-header">
            <button type="button" style ={{width:"45px"}} className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>                        
            </button>
            <a className="navbar-brand" style ={{color:"#413c69",fontWeight:"bolder",fontSize:"25px",marginLeft:"10px"}} href="#">Logo</a>
          </div>
          <div className="collapse navbar-collapse" id="myNavbar">
            <ul className="nav navbar-nav navbar-right">

              
                <li ><Link style ={{color:"#413c69",fontSize:"20px"}} to="/"><span className="glyphicon glyphicon-home"></span> Home</Link></li>
              {/* <li><Link to="/signup"><span className="glyphicon glyphicon-user"></span> {auth.isAuthenticated()? auth.authState.userInfo.name :"Sign Up" }</Link></li> */}
                            <li><Link style ={{color:"#413c69",fontSize:"20px"}} to="/signup"><span className="glyphicon glyphicon-user"></span> 
              {auth.isAuthenticated()? <span onClick={(e)=>{
                console.log("BITCH ")
                e.preventDefault();
              }}> {auth.authState.userInfo.name}</span>:"Sign Up" } </Link></li>

              {
                auth.isAuthenticated() && 
                <li><Link style ={{color:"#413c69",fontSize:"20px"}} to="/editProfile"><span className="glyphicon glyphicon-user"></span>  Edit Profile</Link></li>
              }

              <li><Link style ={{color:"#413c69",fontSize:"20px"}} to="/login"><span className="glyphicon glyphicon-list-alt"></span> 
              {auth.isAuthenticated()? <span onClick={auth.logOut}>{ "Log Out"}</span>:" Login" } </Link></li>
                
            </ul>
          </div>
        </div>
      </nav>);


}
export default NavBar;