import React, { useState, useContext } from 'react';
import axios from "axios";
import SideBar from "./context/SideBar";
import  { AuthContext } from "./context/AuthContext";
import {FetchContext, FetchProvider} from "./context/FetchContext"

function AddEvent() {

    const auth = useContext(AuthContext);
    const fauth = useContext(FetchContext);


    const [FormData, SetFormData] = useState(
        {

        }
    )
    function handleChange(event) {
        const { name, value } = event.target;
        console.log("Agent form", name, value);
        SetFormData((prev) => {
            return {
                ...prev,
                [name]: value
            }
        })

    }

    async function HandleSubmit(e) {
        e.preventDefault();
        console.log("All Event Data ", FormData)

        try
        {
        const del = await fauth.authAxios.post("/events",FormData)
        console.log(del)
        alert("Event Added!")

        document.getElementById("subBut").style.display="none"
        }
        catch(er)
        {
            console.log(er)
        }

        

    }
    return (
        <div>

            {auth.isAuthenticated() && auth.isAgent()?
                <SideBar/>:<></>
            }

            
        <div style={{margin:"auto"}} class="container-fluid" className="container-fluid container FluidStyle">
            <h1 style={{color:"black",paddingBottom:"10px",opacity:"70%"}}>Add New Event</h1>
            <form style={{color:"black"}} onSubmit={HandleSubmit}>

                <input style={{color:"black"}} onChange={handleChange} name="eventName" type="text" placeholder="Name" />
                <input style={{color:"black"}} onChange={handleChange} name="topic" type="text" placeholder="Topic" />
                <input style={{color:"black"}} onChange={handleChange} name="startTime" type="Time" placeholder="Start Time" />
                <input style={{color:"black"}} onChange={handleChange} name="date" type="Date" className="" placeholder="Date" />
                <input style={{color:"black"}} onChange={handleChange} name="meetingNumber" type="text" placeholder="Meeting Number" />
                <input style={{color:"black"}} onChange={handleChange} name="password" type="text" placeholder="Password" />
                <input style={{color:"black"}} onChange={handleChange} name="description" type="text" placeholder="Description" />

                <button style={{color:"white",background:"#413c69"}} id="subBut" type="submit">Submit</button>
            </form>
            </div>
        </div>
      
    )
}
export default AddEvent;