import React, { useState,useEffect,useContext } from "react";
import Event from "./Event"
import axios from 'axios' 
import  { AuthContext } from "../context/AuthContext";
import {FetchContext, FetchProvider} from "../context/FetchContext"
import Pagination from '@material-ui/lab/Pagination';
import CircularProgress from '@material-ui/core/CircularProgress';
import Switch from '@material-ui/core/Switch';

import SideBar from "../context/SideBar";
function Home() {

  const auth = useContext(AuthContext);
  const fauth = useContext(FetchContext);


  const [state, setState] = React.useState({
    isAgent: true,
  });

  const handleChange = (event) => {
    setState({[event.target.name]: event.target.checked });
  };

    const [EventData,SetEventData]=useState(null);
    const [UserEvent,SetUserEvent]=useState([{}]);
    const [UserRole,SetUserRole]=useState(false);
    const [Page,SetPage]=useState(1);


    function handlePage(event, value)
    {
        
        SetPage(value);
        console.log(value)
    }

    useEffect(()=>{
        async function hello()
        {
            console.log("hello");
        
        SetEventData(null)
        const data = await fauth.authAxios.get(`/events/${Page}`);
        

        if (auth.isAuthenticated())
        {
       const role = auth.authState.userInfo.role;

       if (role === "Simple Agent")
       {
           SetUserRole(false)
       }
       else if (role === "Super Agent")
       {
           SetUserRole(true) 
       }

       }

        const temp =[]

        data.data.forEach(element => {
            
            // SetEventData([...EventData, element]);
            temp.push(element)
            
        });
        
        console.log(temp);
        SetEventData(temp);
        // SetUserEvent(subs);
        
        }
       
       
        hello()
    },[Page])


    useEffect(()=>{
        async function hello()
        {
            
            
        
        
        if (!state.isAgent)
        {

        console.log("hello THIS IS AGENT ONE ");
        const user_id=auth.authState.userInfo._id
        const  data = await fauth.authAxios.post("/user/events",{user_id});

        const temp =[]

        data.data.forEach(element => {
            
            
            temp.push(element)
            
        });
        
        console.log(temp);
        SetEventData(temp);
        }
        else
        {
            const data = await fauth.authAxios.get("/events/1");
            const temp =[]

        data.data.forEach(element => {
            
            
            temp.push(element)
            
        });
        
        console.log(temp);
        SetEventData(temp);

        }
        

        console.log(UserEvent)
        

        if (auth.isAuthenticated())
        {
       const role = auth.authState.userInfo.role;

       if (role === "Simple Agent")
       {
           SetUserRole(false)
       }
       else if (role === "Super Agent")
       {
           SetUserRole(true) 
       }

       }

        
        // SetUserEvent(subs);
        
        }
       
        // document.getElementById('zmmtg-root').style.display ="none";
        // document.getElementById('root').style.overflow ="auto"
        hello()
    },[state.isAgent])

    useEffect(()=>{
        async function hello()
        {
            console.log("hello THIS IS SECOND");
            
        const data = await fauth.authAxios.get("/events/1");    
     
        
        if (auth.isAuthenticated())
        {
        const user_id=auth.authState.userInfo._id
        const  subs = await fauth.authAxios.post("/user/events",{user_id});
        SetUserEvent(subs)
        }
        

        console.log(UserEvent)
        

        if (auth.isAuthenticated())
        {
       const role = auth.authState.userInfo.role;

       if (role === "Simple Agent")
       {
           SetUserRole(false)
       }
       else if (role === "Super Agent")
       {
           SetUserRole(true) 
       }

       }

        const temp =[]

        data.data.forEach(element => {
            
            // SetEventData([...EventData, element]);
            temp.push(element)
            
        });
        
        console.log(temp);
        SetEventData(temp);
        // SetUserEvent(subs);
        
        }
       
        // document.getElementById('zmmtg-root').style.display ="none";
        // document.getElementById('root').style.overflow ="auto"
        hello()
    },[])


    return (<div>
            {auth.isAuthenticated() && auth.isAgent()?
                <SideBar/>:<></>
            }
     
        <div className="mobileEvent" >


        {
            auth.isAuthenticated() && 
            <>
        <p style={{color:"black",opacity:"80%"}}>{state.isAgent ? "All Events" : "My Events"}</p>
        <div className="radios">

        <Switch
        checked={state.isAgent}
        onChange={handleChange}
        name="isAgent"

        inputProps={{ 'aria-label': 'secondary checkbox' }}
      />
      

      </div>
      </>
        }
            <div  className="flex-container container">
            


          
            
            {
                
            !EventData &&
            <div style = {{display:"grid",placeItems:"center",justifyContent:"center",alignItems:"center",width:"100%"}}>
            <div>
             <CircularProgress color="secondary" />
             </div>
             </div>
        }

            {EventData &&  EventData.map((Data)=>{
                return(
                <div className="flex-item-left"><Event event ={Data}
                 eventSubs ={state.isAgent} eventRole = {UserRole}
                 eventPass={Data.password} eventJoin={Data.id} eventName={Data.topic}  eventStatus={Data.status} eventTimezone={Data.timezone} eventID={Data._id} eventNo={Data.meetingNumber} /></div>
                );
            })}
            </div>

            </div>

            

            { state.isAgent &&
            <div>
        <Pagination style={{marginTop:"20px"}} count={100} page={Page} onChange={handlePage} size="small" color="primary" /></div>
            }
          
        
            
            </div>

    )
}

export default Home;