import React, { useState,useContext ,useEffect,lazy} from "react";
import  { AuthContext } from "../context/AuthContext";
import {Link} from "react-router-dom"
import {Redirect} from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import Alert from '@material-ui/lab/Alert';
import axios from 'axios'
import Zoom from '../Zoom'
import {FetchContext, FetchProvider} from "../context/FetchContext"
// const Zoom = lazy(() => import('../Zoom'));

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
}));



function Event(props) {

  const auth = useContext(AuthContext);
  const fauth = useContext(FetchContext);

  const [IsSubscribed,SetIsSubscribed]= useState(false)

  const [JoinZoom, SetJoinZoom]=useState(false)
  
  const [redirect, SetRedirect]=useState(false)
  
  const [redirect2, SetRedirect2]=useState(false)

  const [redirect3, SetRedirect3]=useState(false)

  function DetailsClick()
  {
    SetRedirect3(true)
  }

  useEffect(()=>{

    const getData= async()=>{
    
     
    if(auth.isAuthenticated())
    { 


    // const user_id=auth.authState.userInfo._id
    // const subs = await axios.post("https://secure-brushlands-13595.herokuapp.com/user/events",{user_id});
    // console.log(props.eventSubs)
    
    //  if (props.eventSubs.data.includes(props.eventID))
    //  {
    //    SetIsSubscribed(true)
    //    console.log("yesssaaaaaaa");
    //  }

     if (props.eventSubs === false)
     {
       SetIsSubscribed(true)
     }
     else 
     {
      SetIsSubscribed(false)
     }


    }
    }

    console.log("asdssad");
    getData();


  },[props.eventSubs]);


  const classes = useStyles();


  function HandleStart()
{
  SetJoinZoom(true)
}

   async function HandleSusrcribe()
   {
    

     if(auth.isAuthenticated())
    {
      
      if (auth.isAgent())
      {
        SetRedirect2(true)
        return <Redirect to="/editEvent"/>
      }


      if (IsSubscribed)

      {

          console.log("ZOOOOOOOm");
          SetJoinZoom(true)
      }
      else
      {

      const info=
      {
        user_id : auth.authState.userInfo._id,
        meeting_id : props.eventID
      }

      

      try
      {

      const data = await fauth.authAxios.post("/subscribe",info);
      SetIsSubscribed(true)
      
      }
      catch(error)
      {
        console.log(error)
        SetIsSubscribed(false)
      }
    }
      
    }
    else
    { 
      SetRedirect(true); 
       return <Redirect to="/login"/>
      //window.location="/login"
    }
 }

 async function DeleteEvent()
 {

  const deldata =
  {
    id:props.eventID
  }
   try
   {
     const del = await fauth.authAxios.post("/events/delete",deldata)
     console.log(del)
   }
   catch(er)
   {
     console.log(er)
   }
 }


   const EventStyle ={
     padding:"3rem",
     maxWidth:"400px",
     minWidth:"100px",
     background:"white",
     textAlign:"center",
     borderRadius:"5px",
     opacity:"90%"
   }

    return (
      <>
      <div class="container-sm">
          { redirect &&  
             <Redirect to="/login"/>}
             { redirect2 &&  
             <Redirect
            to={{
            pathname: "/editEvent",
            state: { event: props.event }
          }}
        />}

        {redirect3 &&  
             <Redirect
            to={{
            pathname: "/eventDetails",
            state: { event: props.event }
          }}
        />}
       
             
        <div className="" style = {EventStyle}>
        <h2 style={{color:"black"}}>{props.eventName}</h2>
        <p style={{fontSize:"20px", color:"black"}}> {props.eventTimezone}</p>
        <p style={{fontSize:"20px", color:"#66CDAB"}}> {props.eventStatus}</p>
        <form>
          {/* <input
          //   onChange={handleChange}
            type="text"
            placeholder="Hehehehe"
          //  value={name}
          /> */}
          
          <button style={{background:"#6495ED", color:"white"}} className="" onClick={DetailsClick} type="button">Details</button>

          { !auth.isAgent() &&
          <>
          <button style={ IsSubscribed ? {background:"#FA8072" ,color:"white"} : {background:"#66CDAF" ,color:"white"} } className="" type="button" id="subsBtn" onClick={HandleSusrcribe}>{IsSubscribed? "Join" : "Subscribe"}</button>
           </>
          }
          {auth.isAgent() && 
             <>
            <button style={ {background:"#289672" ,color:"white"} } className="" type="button" id="subsBtn" onClick={HandleSusrcribe}>Edit</button>
            <button style={ {background:"#184d47" ,color:"white"} } className="" type="button" id="subsBtn" onClick={HandleStart} >Start Meeting</button>
            </>
          }
          {
            props.eventRole &&
            <button style={{background:"#FA8072", color:"white"}} onClick={DeleteEvent} className="" type="button">Delete</button>
          }
        </form>

        {JoinZoom && <Zoom _id={props.eventJoin} _pass={props.eventPass}  number= {props.eventNo} />}
      </div>
      </div>
      </>
    )
}

export default Event;