import React, { useState,useContext } from "react";
import axios from "axios"
import {AuthContext} from "./context/AuthContext"
import {FetchContext, FetchProvider} from "./context/FetchContext"

function SignUp_3(props){
    console.log(props);
const [phoneNo,setPhoneNo]=useState(props.PhoneNum)
const authContext = useContext(AuthContext);
const[UserData,SetUserData]=useState({ })
const fauth = useContext(FetchContext);

    function handleChange(event){
        const {name,value}=event.target;
        console.log("bti",name,value);
        SetUserData((prev)=>{
            return {
                ...prev,
                [name]:value
            }
        })

    }

async function SubmitHandle(e){
    console.log("yo");
    e.preventDefault();
     const phoneNo = localStorage.getItem('phone');
     const num= parseInt(phoneNo.slice(1,phoneNo.length));
        
        console.log(typeof(num));
     UserData.phoneNo=num;
     console.log(UserData);
        try{
         const data = await fauth.authAxios.post("/signup",UserData); 
         console.log(data);
        
                  const data1 = {
                    token: data.data.token,
                    expiresAt: data.data.expiresAt,
                    userInfo: data.data.userInfo,
                  
                  }
                  console.log(data1);
                  authContext.setAuthState(data1);
                  
     localStorage.removeItem('phone');
        window.location="/";
    }catch(err){
        console.log(err)
    }
}
return (
    <div className="container SignUpForm">
    <h2 >{props.HeaderText}</h2>
    <p>{props.PTxt}</p>
    
    <form onSubmit= {SubmitHandle}>
    
    <input id="entry1" onChange={handleChange} name={props.Entry1} type="text" className="" placeholder={props.Entry1} value={props.PhoneNum} />
    <input id="entry2" onChange={handleChange} name="firstName" type="text" className="" placeholder={props.Entry2}  />
    <input id="entry3" onChange={handleChange} name="lastName"type="text" className="" placeholder={props.Entry3}  />
    <input id="entry4" onChange={handleChange} name="email" type="text" className="" placeholder={props.Entry4}  />
    <input id="entry5" onChange={handleChange} name="DOB" type={props.typ} className="" placeholder={props.Entry5}  />
    <select id="entry6" onChange={handleChange} name="gender" className="custom-select " id={props.UniqId1} >
        <option defaultValue>{props.Select1} </option>
        <option value="Male">{props.Select1_1}</option>
        <option value="Female">{props.Select1_2}</option>
        <option value="Custom">{props.Select1_3}</option>
    </select>
    <select id="entry7" onChange={handleChange} name="country"className="custom-select " id={props.UniqId2} >
        <option defaultValue>{props.Select2} </option>
        <option value="1">{props.Select2_1}</option>
        <option value="2">{props.Select2_2}</option>
        <option value="3">{props.Select2_3}</option>
    </select>





    <select id="entry8" onChange={handleChange} name="city" className="HalfCustom" id={props.UniqId3} >
        <option defaultValue>{props.Select3}</option>
        <option value="1">{props.Select3_1}</option>
        <option value="2">{props.Select3_2}</option>
        <option value="3">{props.Select3_3}</option>
    </select>


    <select id="entry9" onChange={handleChange} name="locality"className="HalfCustom" id={props.UniqId4} >
        <option defaultValue>{props.Select4}</option>
        <option value="1">{props.Select4_1}</option>
        <option value="2">{props.Select4_2}</option>
        <option value="3">{props.Select4_3}</option>
    </select>


    <input id="entry10" onChange={handleChange} name="address" type="text" className="" placeholder={props.Entry6} />
    <input id="entry11" onChange={handleChange} name="password" type="text" className="" placeholder={props.Entry7}  />
    <input
        id="entry69"
        type="text"
        placeholder="cell"
        onChange={handleChange}
        //value={props.PhoneNum}
        disabled
    />

    <button type="submit" className="btn btn-default" id="step3Btn2">Submit</button>
    </form>
    <p>All rights are reserved 2020</p>

</div>
)
}
export default SignUp_3;